import React, { useState, useEffect } from "react";
import Layout from "../../../Components/Layout1/Layout";
import axios from "axios";
import { useParams } from "react-router-dom";
import { Document, Page, pdfjs } from 'react-pdf';
import { useMediaQuery } from 'react-responsive';
import 'react-pdf/dist/esm/Page/AnnotationLayer.css';
import 'react-pdf/dist/Page/TextLayer.css';
pdfjs.GlobalWorkerOptions.workerSrc = `//cdnjs.cloudflare.com/ajax/libs/pdf.js/${pdfjs.version}/pdf.worker.js`;

function Project() {
  const isTab = useMediaQuery({ maxWidth: 768 });
  const isMobile = useMediaQuery({ maxWidth: 600 });
  const [size,setSize]=useState(770);
  // if(isMobile){
  //   setSize(500)
  // }
  // else if(isTab){
  //   setSize(700)
  // }
  // else{
  //  setSize(770)
  // }
  const params = useParams();
  const [numPages, setNumPages] = useState(null);
  const [pdfUrl, setPdfUrl] = useState('');
  const [department, setdepartment] = useState();
  const [faculty, setfaculty] = useState({});

  const getAllFaculty = async () => {
    try {
      const { data } = await axios.get(`/faculty/get-faculty/${params.slug}`);
      setfaculty(data.faculty);
    } catch (error) {
      console.log(error);
    }
  };

  useEffect(() => {
    getAllFaculty();

  //   const fetchPDF = async () => {
  //     const id1="65ded50b5d3404b36d1b87fc"
  //     console.log(faculty.name);
  //     const response = await fetch(
  //       `/faculty/faculty-project_images/${faculty?._id}`
  //     );
  //     const blob = await response.blob();
  //     const url = URL.createObjectURL(blob);
  //     setPdfUrl(url);
  //   };
  // if(faculty){
  //   fetchPDF();
  // }
   
  }, []);
 
  useEffect(() => {
    const fetchPDF = async () => {
      if (faculty._id) {  // Only attempt to fetch if `faculty._id` is available
        try {
        
          const response = await fetch(`/faculty/faculty-project_images/${faculty._id}`);
          const blob = await response.blob();
          const url = URL.createObjectURL(blob);
          setPdfUrl(url);
        } catch (error) {
          console.error('Error fetching PDF: ', error);
        }
      }
    };
  
    fetchPDF();
  }, [faculty._id]);  

  function onDocumentLoadSuccess({ numPages }) {
    setNumPages(numPages);
  }

  return (
    <Layout>
    
        <div className="course-container">
          <div className="text-section">
            <div className="static">
              <h2
                style={{
                  color: "#6B469C",
                  fontSize: "20px",
                  marginTop: "50px",
                }}
              >
                AR2024
              </h2>
              <div className="cont">
                <h1 style={{ fontSize: "25px" }}>Designing with people</h1>
                <p>
                  <strong>Faculty:</strong> {faculty?.name}
                </p>
                <p>
                  <strong>TA:</strong> {faculty?.ta}
                </p>
              </div>
              <h3>{faculty?.project_name}</h3>
              <p className="p1">
                {faculty?.project_details}
              </p>
            </div>
          </div>
          <div className="image-gallery">

          {pdfUrl && (
            <Document
              file={pdfUrl}
              onLoadSuccess={onDocumentLoadSuccess}
              loading={<p>Loading PDF...</p>}
            >
              {Array.from(new Array(numPages), (el, index) => (
                <div key={`page_${index + 1}`}>
                  <Page pageNumber={index + 1}  
                  width={isMobile?500:700} />
                </div>
              ))}
            </Document>
          )}
            {/* <img src={`/faculty/faculty-project_images/${faculty?._id}`} alt="Event Photo" className="image-large" /> */}
           
          </div>
        </div>
     
    </Layout>
  );
}

export default Project;
