import React, { useState } from 'react'
import { a } from 'react-router-dom';
import Layout from '../Components/Layout1/Layout';
import img1 from '../img/left.jpeg';
import img2 from '../img/right.jpeg'
function Homepage() {
  return (
   <Layout>
       <div className="main">
        <div className="left">
          <img src={img1} alt="error" />
           <text className='text1'>FA</text>
           <div className="arc">
            <p className='arct'>ARCHITECTURE</p>
            <a className='learnmore' href={`/dashboard/admin/department/courses/65d5cfa00653e75d82bc8782`}>Learn More</a>
           </div>
         
        </div>
       
        <div className="right">
          <img src={img2}  alt="error" />
          <text className='text2'>FD</text>
          <div className="arc">
            <p className='arct'>DESIGN</p>
            <a className='learnmore' href={`/dashboard/admin/department/courses/65d5d10f4640bcc22bfb30a9`}>Learn More</a>
           </div>
        </div>
      
        </div>
       
   </Layout>
  )
}

export default Homepage
