import React, { useState, useEffect } from "react";
import Layout from "../../../Components/Layout1/Layout";
import photo from "../../../img/pp.png";
import axios from "axios";
import { Select } from "antd";
import { useMediaQuery } from 'react-responsive';
import { useNavigate, useParams, Link } from "react-router-dom";
// import { useId } from "../../../Context/FilterContext";
const { Option } = Select;

function Dept_courses() {
  const isMobile = useMediaQuery({ maxWidth: 768 });
  const params = useParams();
  const [Id, setId] = useState(params.departmentId);
  const [count, setcount] = useState(0);
  const [dName, setdName] = useState("");
  const [faculty, setfaculty] = useState([]);
  // const [photo, setPhoto] = useState("");

  const sendDataToServer = async (value) => {
    try {
      const data = await axios.post("/dept-id", { value });
    } catch (error) {
      console.log(error);
    }
  };

  const getAllFaculty = async () => {
    try {
      const Id1 = params.departmentId;
      setId(Id1);
      // console.log(Id + "--Id");
       const  {data}  = await axios.get(`/faculty/get-specificfaculty/${params.departmentId}`);
      // const { data } = await axios.get("/faculty/get-specificfaculty");
      //  const  {data}  = await axios.get(`/faculty/get-faculty`);
      setfaculty(data.faculty);
      setcount(data.countTotal);
     
    } catch (error) {
      console.log(error);
    }
  };
  const getDepartmentName = async () => {
    try {
      
      const { data } = await axios.get("/department/departmentname");
      // const { data } = await axios.get("/faculty/get-specificfaculty");
      setdName(data.department[0].name);
     
    } catch (error) {
      console.log(error);
    }
  };

  useEffect(() => {
    if (params?.departmentId) {
      sendDataToServer(Id);
      getAllFaculty();
      getDepartmentName();
    }

  }, [params?.departmentId]);
  return (
    <Layout>
      <div className="container">
        <h1 className="dep">Bachelors in {dName}</h1>
        <div className="profiles">
        {
          faculty[0]?.name.includes('|')? (
           <>
          
          <div className="profile-card" style={{width:isMobile ? '60%' : '33%',height:isMobile ? '35vw' : '33%'}} >
            <Link
              to={`/dashboard/admin/course/project/${faculty[0]?.slug}`}
              className="product-link"
            >
              <div className="profile-image">
                <img
                  src={`/faculty/faculty-photo/${faculty[0]?._id}`}
                />
                <div className="overlay">
                  <div className="text"> {faculty[0]?.project_name}</div>
                </div>
              </div>
            </Link>
            <div className="profile-info ">
              <h3>{faculty[0]?.name}</h3>
              <p>
              {/* {dName.slice(0, 2).toUpperCase()} */}
              {faculty[0]?.AR}</p>
            </div>
          </div>
          </>
          ) : (<>
            <div className="profile-card" style={{width:isMobile ? '33%' : '17%',height:isMobile ? '35vw' : '17%'}}>
            <Link
              to={`/dashboard/admin/course/project/${faculty[0]?.slug}`}
              className="product-link"
            >
              <div className="profile-image">
                <img
                  src={`/faculty/faculty-photo/${faculty[0]?._id}`}
                />
                <div className="overlay">
                  <div className="text"> {faculty[0]?.project_name}</div>
                </div>
              </div>
            </Link>
            <div className="profile-info ">
              <h3>{faculty[0]?.name}</h3>
              <p>
              {/* {dName.slice(0, 2).toUpperCase()} */}
              {faculty[0]?.AR}</p>
            </div>
          </div>
          </>)}
        {/* <div className="cta-section">
            <div className="cta-content">
              <h2>Designing with people</h2>
              <button>Learn more </button>
            </div>
        </div> */}
          {faculty?.map((f) => (
           f.name.length>20 ? (<React.Fragment key={f._id}>
            <div className="profile-card" style={{width:isMobile ? '66%' : '33%',height:isMobile ? '35vw' : '33%'}} key={f?._id}>
              <Link
               
                to={`/dashboard/admin/course/project/${f.slug}`}
                className="product-link"
              >
                <div className="profile-image">
                  <img src={`/faculty/faculty-photo/${f?._id}`} alt="error" />
                  <div className="overlay">
                  <div className="text"> {f?.project_name}</div>
                </div>
                </div>
              </Link>
              <div className="profile-info ">
                <h3>{f?.name}</h3>
                <p>
                {/* {dName.slice(0, 2).toUpperCase()} */}
                {f?.AR}</p>
              </div>
            </div>
            </React.Fragment>) : (<React.Fragment key={f._id}>
            <div className="profile-card" style={{width:isMobile ? '33%' : '17%',height:isMobile ? '35vw' : '17%'}} key={f?._id}>
              <Link
               
                to={`/dashboard/admin/course/project/${f.slug}`}
                className="product-link"
              >
                <div className="profile-image">
                  <img src={`/faculty/faculty-photo/${f?._id}`} alt="error" />
                  <div className="overlay">
                  <div className="text"> {f?.project_name}</div>
                </div>
                </div>
              </Link>
              <div className="profile-info ">
                <h3>{f?.name}</h3>
                <p>
                {/* {dName.slice(0, 2).toUpperCase()} */}
                {f?.AR}</p>
              </div>
            </div>
            </React.Fragment>)
          ))}

        </div>
      </div>
    </Layout>
  );
}

export default Dept_courses;
